*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  user-select: text;
}

body {
  background-color: #fff;
  font-family: Inter;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
  outline: none;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.containers {
  max-width: 1440px;
  padding: 0 212px;
  margin: 0 auto;
  width: 100%;
}

.display-n {
  display: none !important;
}

opacity-h {
  opacity: 0 !important;
}

.footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 45px 0;
}

.footer__title {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 75px;
  background: #ffffff;
  border: 1px solid #dce1e5;
}

.header__title {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 39px;
  color: #000000;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.home {
  width: 100%;
  margin: 15px 0 0 0;
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  z-index: 2;
}

.home__wrapper {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.home__blocks {
  margin: 0 0 0 15px;
}

.home__welcome {
  background: #ffffff;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  padding: 48px 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home__welcome-title {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 26px;
  line-height: 31px;
  text-align: center;
  color: #000000;
}

.home__welcome-text {
  margin: 8px 0 0 0;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #818c99;
}

.home__account {
  background: #ffffff;
  border: 1px solid #dce1e6;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 12px 24px;
  width: 550px;
}

.home__account-title {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: #222222;
}

.home__account-line {
  width: calc(100% + 32px);
  margin: 14px 0;
  height: 1px;
  background: #e7e8ec;
}

.home__account-blocks {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

.home__account-btn {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #2d81e0;
  margin: 20px 0 15px 0;
  cursor: pointer;
  background: transparent;
  outline: none;
  border: none;
}

.home__reportList {
  border: 1px solid #dce1e5;
  border-radius: 6px;
  background: #fff;
  padding: 14px 16px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.home__reportList-tabs {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.home__reportList-decor {
  cursor: pointer;
}

.home__reportList-tab {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 32px 0 0;
  cursor: pointer;
}

.home__reportList-tab__a h5 {
  color: #000000 !important;
}

.home__reportList-tab:last-child {
  margin: 0 0 0 0;
}

.home__reportList-tab h5 {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: gray;
  transition: all 0.5s ease;
  margin: 0 6px 0 0;
}

.home__reportList-tab span {
  width: 18px;
  height: 18px;
  background: #ff3347;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
}

.home__reportList-line {
  width: calc(100% + 32px);
  height: 0.5px;
  background: #e7e8ec;
  position: relative;
  left: -16px;
  margin: 14px 0 0 0;
}

.home__reportList-lines {
  width: calc(100% + 32px);
  height: 0.5px;
  background: #e7e8ec;
  position: relative;
  left: -16px;
