  align-items: center;
  justify-content: flex-start;
}

.home__reportList-input {
  width: 100%;
  padding: 0 20px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: #000000;
}

.home__reportList-input::placeholder {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: #818c99;
